Template.JetSetter_reactive.helpers({
	ReactiveDrill: function () {
		Dictionaries = [];

		if (Package["session"]) {
			if (typeof Session.all == 'function') {
			    var referencedKeys = [];
			    var originalKeys = Object.keys(Session.all());

			    originalKeys.forEach(function(targetKey) {
			    		referencedKeys.push({
			    			parent: "Session",
			    			key:    targetKey
			    		})
			    	})

			    Dictionaries.push({
			    	name: "Session",
			    	keys: referencedKeys
			    });
			}
		}
		
		if (Package["meteortoys:toykit"].ToyKit.equals("authenticated", true)) {
			DictNames    = Package["meteortoys:toykit"].MeteorToysData.JetSetter.find().fetch();
			
			DictNames.forEach(function(targetDict) {
				
				dictionaryName = targetDict.name;
				referencedKeys = [];
				originalKeys   = Object.keys(window[targetDict.name].all());

				originalKeys.forEach(function(targetKey) {
					referencedKeys.push({
						parent: dictionaryName,
						key:    targetKey
					})
				})

				Dictionaries.push({
					name: dictionaryName,
					keys: referencedKeys
				});

			});
		}

		return Dictionaries;
	}
});


