delete Session.all
// Initialize Reactive-Dict
MeteorToysDict = Package["meteortoys:toykit"].MeteorToys;

Template.JetSetter.helpers({
    expanded: function () {
        var current = MeteorToysDict.get("JetSetter_current");
        if (current) {
            return "JetSetter_expand"
        }
    },
    JetSetter_oldSession: function () {
    	if (typeof Session.all == 'function') {
    		return false;
    	} else {
    		return true;
    	}
    }
});

Template.JetSetter.events({
	'mouseover': function () {
		$("#JetSetter").addClass("JetSetter_previewMode");
	},
	'mouseout': function () {
		$("#JetSetter").removeClass("JetSetter_previewMode");
	},
})

JetSetter = {};

CloseJetSetter = function () {
	if (Package["meteortoys:toykit"].MeteorToys.get("JetSetter_current")) {
        Package["meteortoys:toykit"].MeteorToys.set("JetSetter_current");    
    } else {
        MeteorToys.close();
    }
    
}

